import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoantermlistComponent } from './loantermlist.component';
import { LoantermaddComponent } from './loantermadd.component';


const routes: Routes = [
  { path: '', component: LoantermlistComponent },
  { path: 'editloanTerm/:id', component: LoantermaddComponent },
   { path: 'loanTermAdd', component: LoantermaddComponent }  
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LoanTermRoutingModule { }
